import React ,{Component}  from 'react';
import {Grid, Cell} from 'react-mdl';
import Education from './education';
import Experience from './experience';
import Skill from './skill';

class Resume extends Component{
    constructor (props) {
        super(props)
        this.state = {message: ''}
        this.handelChange = this.handelChange.bind(this);
    }
    handelChange (event) {
        this.setState({message: event.target.value});
    }
    render() {
        return(
            <div>
                <Grid>
                    <Cell  col={4} >
                        <div style={{textAlign:'center'}}>
                        <img 
                        src="https://cdn3.iconfinder.com/data/icons/user-avatars-1/512/users-2-512.png"
                        alt="avatar"
                        style={{height: '180px'}}
                        />
                        </div>

                        <h2 style={{paddingTop:'1em',}}>Dandan</h2>
                        <h4 style={{color:'grey'}}>Programmer</h4>
                        <hr style={{borderTop: '3px solid #833fb2',width:'50%'}}/>
                        <p>  JAVA34nodejsHTMLCSSJAVASCRIPTexpressSERVERVUEside  project  Restfulapitokenapi</p>
                        <hr style={{borderTop: '3px solid #833fb2',width:'50%'}}/>
                        <h5>Address</h5>
                        <p>TWHsinchu/Taipei</p>
                        <h5>Phone</h5>
                        <p>(+886)983914037</p>
                        <h5>Email</h5>
                        <p>dandanxo.1996@gamil.com</p>
                        <h5>Blog</h5>
                        <p>blog.dandan.tw</p>
                        <hr style={{borderTop: '3px solid #833fb2',width:'50%'}}/>
                    </Cell>
                    <Cell col={8} className="resume-right-col">
                        <h2>Education</h2>
                        <h4>University</h4>
                        <Education 
                        startYear={2014}
                        endYear={2018}
                        department=' Information Management Department'
                        schoolName=' National Kaohsiung University of Science and Technology'
                        schoolDescription=''
                        />
                        <h4>High school</h4>
                        <Education 
                        startYear={2011}
                        endYear={2014}
                        department=""
                        schoolName=" National HsinChu Commercial & Vocational High School"
                        schoolDescription=''
                        />
                        <hr style={{borderTop:'3px solid #e22947'}} />
                        <h2>Experience</h2>
                        <Experience
                         startYear={2018+'/'+1}
                         endYear={2018+'/'+6}
                         department=" Elise"
                         jobName="-"
                         jobDescription="API"
                        />
                         <Experience
                         startYear={2016+'/'+7}
                         endYear={2016+'/'+8}
                         department=""
                         jobName=""
                         jobDescription=""
                        />
                        <hr style={{borderTop:'3px solid #e22947'}} />
                        <h2>Skill</h2>
                        <Skill skill='Java' progress={85}/>
                        <Skill skill='Javascript' progress={90}/>
                        <Skill skill='Html/Css' progress={83}/>
                        <Skill skill='NodeJS' progress={90}/>
                        <Skill skill='Vue.js' progress={80}/>
                        <Skill skill='React.js' progress={50}/>
                        <Skill skill='mongodb/firebase' progress={40}/>
                        <hr style={{borderTop:'3px solid #e22947'}} />
                        <p>Interesting test</p>
                        <textarea type="text" onChange={this.handelChange} value={ this.state.message} />
                <p>{this.state.message}</p>
                    </Cell>
                </Grid>
            </div>
        );
    }
}

export default Resume;


